"use client";

import { useEffect, useState } from "react";
import { useParams, useRouter } from "next/navigation";
import axios from "axios";

// Îç∞Ïù¥ÌÑ∞ ÌÉÄÏûÖ Ï†ïÏùò
interface Caravan {
  id: string;
  name: string;
  description: string;
  location: string;
  pricePerDay: number;
  images: string[];
  hostId: string;
}

export default function CaravanDetailPage() {
  const { id } = useParams();
  const router = useRouter();

  // ÏÉÅÌÉú Í¥ÄÎ¶¨
  const [caravan, setCaravan] = useState<Caravan | null>(null);
  const [loading, setLoading] = useState(true);
  
  // ÏòàÏïΩ Í¥ÄÎ†® ÏÉÅÌÉú
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [totalPrice, setTotalPrice] = useState(0);
  const [processing, setProcessing] = useState(false); // Î°úÎî© ÏÉÅÌÉú (Ï§ëÎ≥µ ÌÅ¥Î¶≠ Î∞©ÏßÄ)
  const [showPaymentModal, setShowPaymentModal] = useState(false);
  const [paymentMethod, setPaymentMethod] = useState("CARD"); // Í∞ÄÏÉÅ Í≤∞Ï†ú ÏàòÎã® ÏÉÅÌÉú

  // 1. Ïπ¥ÎùºÎ∞ò ÏÉÅÏÑ∏ Ï†ïÎ≥¥ Î∂àÎü¨Ïò§Í∏∞
  useEffect(() => {
    if (!id) return;
    
    const fetchCaravan = async () => {
      try {
        const response = await axios.get(`http://localhost:3001/api/caravans/${id}`);
        setCaravan(response.data);
      } catch (error) {
        console.error("Ïπ¥ÎùºÎ∞ò Ï†ïÎ≥¥ Î°úÎî© Ïã§Ìå®:", error);
        alert("Ïπ¥ÎùºÎ∞ò Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§.");
      } finally {
        setLoading(false);
      }
    };

    fetchCaravan();
  }, [id]);

  // 2. ÎÇ†Ïßú Î≥ÄÍ≤Ω Ïãú Ï¥ù Í∞ÄÍ≤© ÏûêÎèô Í≥ÑÏÇ∞
  useEffect(() => {
    if (startDate && endDate && caravan) {
      const start = new Date(startDate);
      const end = new Date(endDate);
      
      const diffTime = Math.max(0, end.getTime() - start.getTime());
      const diffDays = Math.ceil(diffTime / (1000 * 3600 * 24));

      if (diffDays > 0) {
        setTotalPrice(diffDays * caravan.pricePerDay);
      } else {
        setTotalPrice(0);
      }
    }
  }, [startDate, endDate, caravan]);

  // 3. 'ÏòàÏïΩ Î∞è Í≤∞Ï†úÌïòÍ∏∞' Î≤ÑÌäº ÌÅ¥Î¶≠ Ìï∏Îì§Îü¨ -> Î™®Îã¨ Ïó¥Í∏∞
  const handleOpenPaymentModal = () => {
    // Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨
    if (!startDate || !endDate) {
      alert("Ï≤¥ÌÅ¨Ïù∏ Î∞è Ï≤¥ÌÅ¨ÏïÑÏõÉ ÎÇ†ÏßúÎ•º ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.");
      return;
    }
    if (totalPrice <= 0) {
      alert("Ïò¨Î∞îÎ•∏ ÎÇ†Ïßú Î≤îÏúÑÎ•º ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.");
      return;
    }
    // Î°úÍ∑∏Ïù∏ ÌôïÏù∏
    if (!localStorage.getItem("user")) {
      alert("Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌïú ÏÑúÎπÑÏä§ÏûÖÎãàÎã§.");
      router.push("/auth/login");
      return;
    }

    setShowPaymentModal(true); // Î™®Îì† Í≤ÄÏÇ¨Î•º ÌÜµÍ≥ºÌïòÎ©¥ Î™®Îã¨ÏùÑ ÏóΩÎãàÎã§.
  };

  // 4. Î™®Îã¨ ÎÇ¥ 'Í≤∞Ï†úÌïòÍ∏∞' Î≤ÑÌäº ÌÅ¥Î¶≠ Ìï∏Îì§Îü¨ -> Ïã§Ï†ú ÏòàÏïΩ Î∞è Í≤∞Ï†ú Ï≤òÎ¶¨
  const handleConfirmPayment = async () => {
    const storedUser = localStorage.getItem("user");
    if (!storedUser || !caravan) return;
    
    const user = JSON.parse(storedUser);

    setProcessing(true);
    try {
      // 1. ÏòàÏïΩ ÏÉùÏÑ± API Ìò∏Ï∂ú
      const reservationResponse = await axios.post("http://localhost:3001/api/reservations", {
        caravanId: id,
        guestId: user.id,
        startDate,
        endDate,
        totalPrice,
      });

      const newReservation = reservationResponse.data;

      // 2. (Í∞ÄÏÉÅ) Í≤∞Ï†ú Ï≤òÎ¶¨ API Ìò∏Ï∂ú
      await axios.post('http://localhost:3001/api/payments', {
        amount: newReservation.totalPrice,
        method: paymentMethod, // ÏÑ†ÌÉùÎêú Í≤∞Ï†ú ÏàòÎã®
        reservationId: newReservation.id,
        userId: user.id,
      });
      
      // 3. ÏÑ±Í≥µ Ï≤òÎ¶¨
      setShowPaymentModal(false);
      alert('ÏòàÏïΩÏù¥ ÌôïÏ†ïÎêòÏóàÏäµÎãàÎã§! ÎÇ¥ ÏòàÏïΩ ÌéòÏù¥ÏßÄÎ°ú Ïù¥ÎèôÌï©ÎãàÎã§.');
      router.push(`/my/reservations`);

    } catch (error: any) {
      console.error("ÏòàÏïΩ ÎòêÎäî Í≤∞Ï†ú Ï≤òÎ¶¨ Ïã§Ìå®:", error);
      // Ïã§Ìå® Ïãú ÏÇ¨Ïö©ÏûêÏóêÍ≤å ÏïåÎ¶º
      const errorMessage = error.response?.data?.message || "ÏòàÏïΩ Ï≤òÎ¶¨ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.";
      alert(errorMessage);
    } finally {
      setProcessing(false);
    }
  };

  if (loading) return <div className="text-center py-20">Î°úÎî© Ï§ë...</div>;
  if (!caravan) return <div className="text-center py-20">Ïπ¥ÎùºÎ∞òÏùÑ Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.</div>;

  return (
    <div className="max-w-6xl mx-auto p-6">
      <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
        
        {/* --- [ÏôºÏ™Ω] Ïπ¥ÎùºÎ∞ò ÏÉÅÏÑ∏ Ï†ïÎ≥¥ --- */}
        <div className="md:col-span-2 space-y-6">
          <div className="w-full h-80 bg-gray-200 rounded-xl flex items-center justify-center text-gray-500 text-lg">
            {caravan.images && caravan.images.length > 0 ? "Ïù¥ÎØ∏ÏßÄ Ïä¨ÎùºÏù¥Îçî" : "Ïù¥ÎØ∏ÏßÄ ÏóÜÏùå"}
          </div>
          <div>
            <h1 className="text-3xl font-bold text-gray-900 mb-2">{caravan.name}</h1>
            <p className="text-gray-500">üìç {caravan.location}</p>
          </div>
          <div className="border-t pt-6">
            <h2 className="text-xl font-semibold mb-4">Ïπ¥ÎùºÎ∞ò ÏÜåÍ∞ú</h2>
            <p className="text-gray-700 leading-relaxed whitespace-pre-wrap">{caravan.description}</p>
          </div>
          <div className="border-t pt-6">
            <h2 className="text-xl font-semibold mb-4">Ìò∏Ïä§Ìä∏ Ï†ïÎ≥¥</h2>
            <p className="text-gray-600">Ìò∏Ïä§Ìä∏ ID: {caravan.hostId}</p>
          </div>
        </div>

        {/* --- [Ïò§Î•∏Ï™Ω] ÏòàÏïΩ ÏúÑÏ†Ø --- */}
        <div className="md:col-span-1">
          <div className="sticky top-8 bg-white border border-gray-200 rounded-xl shadow-lg p-6">
            <div className="flex justify-between items-end mb-6">
              <span className="text-2xl font-bold text-gray-900">‚Ç©{caravan.pricePerDay.toLocaleString()}</span>
              <span className="text-gray-500 mb-1">/ 1Î∞ï</span>
            </div>

            <div className="space-y-4">
              <div className="border rounded-lg">
                <div className="border-b p-3">
                  <label className="block text-xs font-bold text-gray-800 uppercase">Ï≤¥ÌÅ¨Ïù∏</label>
                  <input type="date" value={startDate} onChange={(e) => setStartDate(e.target.value)} className="w-full outline-none text-gray-600 mt-1 bg-transparent" required />
                </div>
                <div className="p-3">
                  <label className="block text-xs font-bold text-gray-800 uppercase">Ï≤¥ÌÅ¨ÏïÑÏõÉ</label>
                  <input type="date" value={endDate} onChange={(e) => setEndDate(e.target.value)} min={startDate} className="w-full outline-none text-gray-600 mt-1 bg-transparent" required />
                </div>
              </div>

              <button
                onClick={handleOpenPaymentModal}
                disabled={processing}
                className={`w-full py-3 rounded-lg text-white font-bold text-lg transition ${
                  processing || totalPrice <= 0
                    ? "bg-gray-400 cursor-not-allowed" 
                    : "bg-rose-600 hover:bg-rose-700"
                }`}
              >
                ÏòàÏïΩ Î∞è Í≤∞Ï†úÌïòÍ∏∞
              </button>
            </div>

            {totalPrice > 0 && (
              <div className="mt-6 pt-4 border-t space-y-2">
                <div className="flex justify-between text-gray-600">
                  <span>‚Ç©{caravan.pricePerDay.toLocaleString()} x {(totalPrice / caravan.pricePerDay)}Î∞ï</span>
                  <span>‚Ç©{totalPrice.toLocaleString()}</span>
                </div>
                <div className="flex justify-between font-bold text-lg text-gray-900 border-t pt-4 mt-2">
                  <span>Ï¥ù Ìï©Í≥Ñ</span>
                  <span>‚Ç©{totalPrice.toLocaleString()}</span>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Í≤∞Ï†ú ÌôïÏù∏ Î™®Îã¨ */}
      {showPaymentModal && caravan && (
        <div className="fixed inset-0 bg-black bg-opacity-60 flex items-center justify-center z-50">
          <div className="bg-white rounded-xl shadow-xl p-8 max-w-lg w-full transform transition-all">
            <h2 className="text-2xl font-bold text-center mb-6">Í≤∞Ï†ú ÌôïÏù∏</h2>
            
            <div className="space-y-4">
              <div className="p-4 border rounded-lg">
                <p className="text-lg font-semibold">{caravan.name}</p>
                <p className="text-sm text-gray-600">{caravan.location}</p>
              </div>
              <div className="p-4 border rounded-lg">
                <p className="text-sm text-gray-500">ÏùºÏ†ï</p>
                <p className="font-medium">{startDate} ~ {endDate}</p>
              </div>
              <div className="p-4 border rounded-lg bg-gray-50">
                <p className="text-sm text-gray-500">Ï¥ù Í≤∞Ï†ú Í∏àÏï°</p>
                <p className="text-2xl font-bold text-indigo-600">‚Ç©{totalPrice.toLocaleString()}</p>
              </div>

              {/* Í≤∞Ï†ú ÏàòÎã® ÏÑ†ÌÉù */}
              <div className="pt-4">
                <h3 className="text-lg font-semibold mb-3">Í≤∞Ï†ú ÏàòÎã®</h3>
                <div className="flex space-x-4">
                  <label className="flex items-center p-3 border rounded-lg cursor-pointer flex-1">
                    <input type="radio" name="paymentMethod" value="CARD" checked={paymentMethod === 'CARD'} onChange={(e) => setPaymentMethod(e.target.value)} className="h-4 w-4 text-indigo-600 border-gray-300 focus:ring-indigo-500"/>
                    <span className="ml-3 text-sm font-medium text-gray-700">Ïã†Ïö©Ïπ¥Îìú</span>
                  </label>
                  <label className="flex items-center p-3 border rounded-lg cursor-pointer flex-1">
                    <input type="radio" name="paymentMethod" value="BANK_TRANSFER" checked={paymentMethod === 'BANK_TRANSFER'} onChange={(e) => setPaymentMethod(e.target.value)} className="h-4 w-4 text-indigo-600 border-gray-300 focus:ring-indigo-500"/>
                    <span className="ml-3 text-sm font-medium text-gray-700">Î¨¥ÌÜµÏû•ÏûÖÍ∏à</span>
                  </label>
                </div>
              </div>
            </div>

            <div className="flex justify-end space-x-4 mt-8">
              <button
                onClick={() => setShowPaymentModal(false)}
                className="px-6 py-2 rounded-lg text-gray-700 bg-gray-200 hover:bg-gray-300 transition"
                disabled={processing}
              >
                Ï∑®ÏÜå
              </button>
              <button
                onClick={handleConfirmPayment}
                className={`px-8 py-2 rounded-lg text-white font-bold transition ${
                  processing 
                    ? "bg-indigo-400 cursor-wait" 
                    : "bg-indigo-600 hover:bg-indigo-700"
                }`}
                disabled={processing}
              >
                {processing ? "Í≤∞Ï†ú Ï§ë..." : "Í≤∞Ï†úÌïòÍ∏∞"}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}